import area from '../../common/js/area.js'
import model from '../../components/model/model.vue'
import {
	http
} from '../../common/js/index.js'
import {
	md5
} from '../../common/js/md5.js'
export default {
	data() {
		return {
			Color: '#DE2910',
			value: '',
			backColor: '#FFFFFF',
			date: '',
			day: '',
			area: area,
			show: false,
			show1: false,
			show2: false,
			start: '',
			end: '',
			index: 0,
			airList: []
		}
	},
	components: {
		model
	},
	onLoad(option) {
		//刷新保留用户uid
		if (this.getRequest('uid')) {
			var uids = this.getRequest('uid')
			this.$store.commit('changeUid', uids)
		}
		this.airList = JSON.parse(option.list)
		this.date = option.date
		this.day = option.day
	},
	methods: {
		getRequest(variable) {
			var query = window.location.search.substring(1);
			var vars = query.split("&");
			for (var i = 0; i < vars.length; i++) {
				var pair = vars[i].split("=");
				if (pair[0] == variable) {
					return pair[1];
				}
			}
			return (false);
		},
		goRoom() {

		},
		addZero(str) {
			str = str > 9 ? str : '0' + str
			return str
		},
		getDay(day) {
			switch (day) {
				case 0:
					day = "星期天"
					break
				case 1:
					day = "星期一"
					break
				case 2:
					day = "星期二"
					break
				case 3:
					day = "星期三"
					break
				case 4:
					day = "星期四"
					break
				case 5:
					day = "星期五"
					break
				case 6:
					day = "星期六"
			}
			return day
		},
		choiceStart() {
			this.show1 = true
		},
		confirm1(e) {
			this.start = e[1].name
			this.show1 = false
		},
		cancel1() {
			this.show1 = false
		},
		choiceEnd() {
			this.show2 = true
		},
		confirm2(e) {
			this.end = e[1].name
			this.show2 = false
		},
		cancel2() {
			this.show2 = false
		},
		del(k) {
			this.show = true
			this.index = k
		},
		cancel() {
			this.show = false
		},
		confirm() {
			this.show = false
			this.listData.splice(this.index, 1)
		},
		goList() {
			uni.navigateTo({
				url: '../airList/airList'
			})
		},
		goType(k,item) {
			uni.navigateTo({
				url: '../airList/airList?type=' + k + '&list=' + JSON.stringify(this.airList) + '&index=' + item
			})
		}
	}
}
